import React from "react";

const List = ({ people, setPeople }) => {
  const remove = (id) => {
    people = people.filter((person) => person.id !== id);
    setPeople(people);
  };

  return (
    <>
      {people.map((person) => {
        const { id, name, age, image } = person;
        return (
          <article key={id} className="person">
            <img src={image} alt={name} />
            <div>
              <h4>{name}</h4>
              <p>{age}</p>
              <button className="btn" onClick={() => remove(id)}>
                Remove
              </button>
            </div>
          </article>
        );
      })}
    </>
  );
};

export const AddBirthday = ({ people, setPeople }) => {
  const [name, setName] = React.useState("");
  const [age, setAge] = React.useState("");
  const [image, setImage] = React.useState("");

  const add = (name, age, image) => {
    const id = people.length + 1;
    const tao = { id, name, age, image };
    if (name && age && image) {
      setPeople((person) => {
        return [...person, tao];
      });
      setName("");
      setAge("");
      setImage("");
    }
  };

  return (
    <>
      <article className="person">
        <label htmlFor="name">Name : </label>
        <input
          type="text"
          name="name"
          value={name}
          onChange={(e) => {
            setName(e.target.value);
          }}
        />
        <label htmlFor="age">Age : </label>
        <input
          type="number"
          name="age"
          id=""
          value={age}
          onChange={(e) => {
            setAge(e.target.value);
          }}
        />
        <label htmlFor="image">Image url: </label>
        <input
          type="text"
          name="image"
          value={image}
          onChange={(e) => {
            setImage(e.target.value);
          }}
        />
        <button className="btn" onClick={() => add(name, age, image)}>
          Add Birthday
        </button>
      </article>
    </>
  );
};

export default List;
